.container{
    display: flex;
    flex-direction: column;
    gap: 2rem;
    margin-bottom: 3.5rem;
}
.title {
    font-size: 0.875rem;
    line-height: 1.25rem;
    text-transform: uppercase;
    font-weight: lighter;
    color: var(--yellow);
}

.description {
    font-size: 3rem;
    line-height: 1;
    text-transform: uppercase;
    font-weight: bold;
}
@media screen and (max-width:768px) {
    .container{
        gap: 1rem;
        margin-bottom: 2.5rem;
    }
    
    .title {
        font-size: 0.775rem;
        line-height: 0.75rem;
    }
    
    .description {
        font-size: 2rem;
        line-height: 0.5;
    }
}