    flex-direction: column;
    gap: 0.75rem;
}

.item1{

    display: flex;
    justify-content: center;
    align-items: center;
}

.contactList{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
}

.phone{
    display: flex;
